import { IInfoInitialState } from "./types";
import { createSlice } from "@reduxjs/toolkit";

const initialState: IInfoInitialState = {
  carsData: null,
  categoriesData: null,
  rateData: null,
  rateTypeData: null,
};

export const infoSlice = createSlice({
  name: "info",
  initialState,
  reducers: {
    setCars: (state, action) => {
      state.carsData = action.payload;
    },
    setCategories: (state, action) => {
      state.categoriesData = action.payload;
    },
    setRate: (state, action) => {
      state.rateData = action.payload;
    },
    setRateType: (state, action) => {
      state.rateTypeData = action.payload;
    },
  },
});

export const { setCars, setCategories, setRate, setRateType } =
  infoSlice.actions;

export default infoSlice.reducer;
