import React, { useEffect } from "react";
import InputAutocomplete from "../../components/InputAutocomplete/InputAutocomplete";
import plugImage from "../../assets/images/car.png";
import "./styles.scss";
import ReactPaginate from "react-paginate";
import { RootState, useAppDispatch } from "../../redux";
import { useSelector } from "react-redux";
import { getCars } from "../../redux/reducers/info/infoThunkCreators";

const CarsPage = () => {
  const dispatch = useAppDispatch();
  const { carsData } = useSelector((state: RootState) => state.info);

  useEffect(() => {
    dispatch(getCars({ offset: 0, limit: 6 }));
  }, []);

  const handleSetCategory = () => {
    console.log("HandleSetRate");
  };

  return (
    <div className="bg-content">
      <div className="main-wrapper cars-page">
        <div className="page-title"> </div>
        <div className="card">
          <div className="card__top filters">
            <div className="filters__list">
              <InputAutocomplete
                name="rate"
                placeholder=""
                arrayData={["string2", "string1"]}
                handleSet={handleSetCategory}
                selectedValue={""}
              />
            </div>
            <div className="filters__action">
              <button className="button"></button>
            </div>
          </div>
          <div className="cars">
            {carsData?.data?.map((item, index) => (
              <div className="cars__item">
                <img
                  className="car-image"
                  alt=""
                  crossOrigin="anonymous"
                  referrerPolicy="origin"
                  src={item.thumbnail?.path}
                  onError={({ currentTarget }) => {
                    currentTarget.onerror = null;
                    currentTarget.src = plugImage;
                  }}
                  width="138"
                  height="63"
                />
                <div className="name-model">{item?.name}</div>
                <div className="info">
                  <div className="info__category">
                    <span className="text_black">:</span>{" "}
                    {item?.categoryId?.name}
                  </div>
                  <div className="info__price">
                    <span className="text_black">:</span> {item.priceMin} -{" "}
                    {item.priceMax}
                  </div>
                  <div className="info__color">
                    <span className="text_black">:</span>{" "}
                    {item?.colors?.map((color) => color + "; ")}
                  </div>
                </div>
                <div className="description">{item?.description}</div>
                <button className="button"></button>
              </div>
            ))}
          </div>
          <div className="card__bottom">
            <ReactPaginate
              pageCount={100}
              forcePage={4}
              pageRangeDisplayed={2}
              marginPagesDisplayed={1}
              className="paginate"
              nextLabel=""
              previousLabel=""
              activeLinkClassName="paginate__active"
              pageClassName="paginate__page"
              previousClassName="paginate__prev-page"
              nextLinkClassName="paginate__next-page"
            />
          </div>
        </div>
      </div>
    </div>
  );
};

export default CarsPage;
