import React from "react";
import { useNavigate } from "react-router-dom";
import "./styles.scss";

interface IPropsErrorPage {
  typeError: string;
  message?: string;
  recommendation?: string;
}

const ErrorPage = ({
  typeError,
  message = "-   ",
  recommendation = "  ",
}: IPropsErrorPage) => {
  const navigate = useNavigate();
  return (
    <div className="bg-content error-page">
      <div className="error-page__type">{typeError}</div>
      <div className="error-page__message">{message}</div>
      <div className="error-page__recommendation">{recommendation}</div>
      <button className="button" onClick={() => navigate(-1)}>
        
      </button>
    </div>
  );
};

export default ErrorPage;
